* {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font-family: "Roboto";
}


:root {
  --hover-color: #F4AC45;
  --navbar-bg-color: rgb(40, 43, 40);
  --transition-links: 0.4s ease;
  --transition: 1s ease;
}

@font-face {
  font-family: "Roboto";
  src: url("./fonts/Roboto-Regular.ttf") format("ttf");
}


/*** Global ***/

h2 {
  color: var(--navbar-bg-color);
}

.line {
  width: 4rem;
  height: 3px;
  background-color: var(--hover-color);
  border-radius: 2px;;
}

.badge {
  background: #F4AC45;
  padding: 2px 3px;
  padding-bottom: 3px;
  border-radius: 0.1rem;
  color: var(--navbar-bg-color);
}

.rubrics {
  margin-top: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
  gap: 100px;
}

.rubrics section {
  width: 80%;
  display: flex;
  flex-direction: column;
  gap: 21px;
  overflow: hidden;
}


section h2 {
  font-size: 2.5rem;
}

.icons-center {
  position: relative;
  top: 2px;
}

/*** App ***/

.App {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.right-part {
  flex: 4;
}

/*** Banner ***/

.banner {
  width: 100vw;
  height: 10vh;
  background: rgb(30, 30, 30);
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner__name {
  font-size: 1rem;
  color: lightgray;
}

/*** Nav ***/

.navbar {
  position: sticky;
  top: 0;
  left: 0;
  height: 100vh;
  flex: 1;
  max-width: 300px;
  min-width: 150px;
  background: var(--navbar-bg-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: lightgray;
}

.navbar__container {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.navbar__container__photo {
  width: 8vw;
  height: 8vw;
  overflow: hidden;
  border-radius: 50%;
  border: 7px solid #F4AC45;
  margin-top: 10px;
  background-image: url("./img/photo.jpg");
  background-size: contain;
}

.navbar__container__photo img {
  width: 10vw;
  height: 10vw;
}

.navbar__container__name {
  font-size: 1.2rem;
}

.navbar__container__socials {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 10px;
}

.navbar__container__socials a {
  font-size: 1.6rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background-color: rgb(41, 54, 87);
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar__container__socials a:hover {
  color: var(--hover-color);
  transform: scale(120%);
}

.navbar__container__links_container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar__container__links_container__links {
  list-style-type: none;
  width: 100%;
  font-size: 1.2rem;
  height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.navbar__container__links_container__links a {
  display: inline-block;
}

.navbar a {
  transition: var(--transition-links);  
  transform: scale(100%);
  color: lightgray;
  text-decoration: none;
}

.navbar a:hover {
  color: var(--hover-color);
  transform: scale(120%);
}

/*** Home ***/

.home {
  position: relative;
  height: 100vh;
}

.home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: url('./img/background_pc.jpg');
  background-size: cover;
  background-position-y: center;
  background-attachment: fixed;
  z-index: -10;
}

.home__name {
  position: absolute;
  left: 8%;
  bottom: 150px;
  color: lightgray;
  user-select: none;
  transition: var(--transition);
  opacity: 100%;
}

.home__name__job {
  position: relative;
  display: flex;
  flex-direction: row;
  gap: 4px;
}

.home__name__job > p {
  display: flex;
  gap: 8px;
}

.home__name__job > p > span:last-child {
  display: flex;
  gap: 3px;
}

.home__name__job__cursor {
  display: inline-block;
  width: 4px;
  height: 80%;
  background: lightgray;
  animation: cursor-blink 0.4s linear 0s infinite alternate-reverse;
  align-self: center;
}

@keyframes cursor-blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100%;
  }
}

.home h1 {
  font-size: 4rem;
}

.home p {
  font-size: 2rem;
  position: relative;
}

.home__name-underline {
  padding-bottom: 3px;
  border-bottom: 2px solid var(--hover-color);
}

/*** Burger ***/

.navbar__burger {
  display: none;
}


/*** Contact ***/

.contact {
  opacity: 0;
  transition: var(--transition);
}

.contact-appear {
  opacity: 100%;
}

.contact__informations {
  align-self: center;
  height: 200px;
  width: 95%;
  box-shadow: 3px 3px 10px rgb(200, 200, 200);
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

/*** About ***/

.about {
}

.about__container {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
}

.about__infos {
  opacity:0;
  transition: var(--transition);
  transform: translateX(20%);
}

.about__infos-appear {
  opacity: 100%;
  transform: translateX(0);
}

.about__photo {
  opacity: 0;
  transform: translateX(-20%);
  transition: var(--transition);
}

.about__photo-appear {
  opacity: 100%;
  transform: translateX(0);
}

.about__job {
  color: var(--hover-color);
  font-size: 2rem;
}

.about__infos ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

/*** Skills ***/

.skills {
  margin-bottom: 50px;
}

.skills__bars {
  width: 100%;
  display: flex;
  gap: 10px;
}

.skills__bars > div {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 30px;
  transition: var(--transition);
  opacity: 0;
  transform: translateY(20%);
}

.skills__bars > div:last-child {
  transition-delay: 0.3s;
}

.skills__bars > div.skills__bars-appear {
  opacity: 100%;
  transform: translateY(0%);
}

/*** ProgressBar ***/

.progress_bar {
  width: 100%;
}

.progress_bar__background {
  height: 10px;
  background-color: lightgray;
}

.progress_bar__foreground {
  height: 100%;
  transform-origin: left;
  transform: scaleX(0);
  background-color: var(--hover-color);
  transition: var(--transition);
}

.progress_bar__foreground-appear {
  transform: scaleX(100%);
}

.progress_bar__legend {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/*** Resume ***/

.education {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.diplome > p {
  margin-top: 5px;
}

.education__title {
  color: var(--hover-color);
  font-size: 1.2rem;
  margin-bottom: 3px;
}

.education__date {
  display: inline;
  padding: 2px 4px;
  background-color: lightgray;
}

.education__location {
  font-style: italic;
}


@media screen and (max-width: 1024px) {

  section {
    border: none;
  }

  /*** Nav ***/
  .navbar {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    height: 100vh;
    width: 30vw;  
    max-width: 300px;
    min-width: 222px;
    background: var(--navbar-bg-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: lightgray;
    transform: translateX(0%);
    transition: 0.5s ease-in-out;
    z-index: 100;
    box-shadow: 10px 0px 20px rgba(0, 0, 0, 0.5);
  }

  .navbar-close_navbar {
    transform: translateX(-100%);
    box-shadow: none;
  }

  .navbar__container__photo {
    width: 12vw;
    height: 12vw;
    overflow: hidden;
    border-radius: 50%;
    border: 4px solid #F4AC45;
    margin-top: 10px;
    background-image: url("./img/photo.jpg");
    background-size: contain;
  }

  .navbar__burger {
    padding: 10px 5px;
    border-radius: 0 5px 5px 0;;
    background-color: var(--navbar-bg-color);
    display: block;
    position: absolute;
    top: 50px;
    right: 0;
    transform: translateX(99.2%);
  }
  
  .burger {
    width: 32px;
    height: 18px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
  }

  .burger div {
    background-color: white;
    width: 100%;
    height: 2px;
  }

  /*** Home ***/

  .home__name h1 {
    font-size: 3rem;
  }

  .home__name p {
    font-size: 1.5rem;
  }

  /*** About ***/
  .about__container {
    flex-direction: column;
  }

  /*** Skills ***/
  .skills__bars {
    flex-direction: column;
    gap: 30px;
  }
}

@media screen and (max-width: 512px) {
  

  
  .home::before {
    background-attachment: scroll;
  }

}